import ben from 'ben'
import { shallow } from 'enzyme'

import Picker1 from '../src/Picker'
import Picker2 from '../src/Picker.2'

const createPicker1 = shallow(
  <Picker1>
    <Option value="one" />
    <Option value="two" />
    <Option value="three" />
  </Picker1>
)

const createPicker2 = shallow(
  <Picker2>
    <Option value="one" />
    <Option value="two" />
    <Option value="three" />
  </Picker2>
)

test('benchmark', t => {
  let ms1 = ben(10000, createPicker1)
  let ms2 = ben(10000, createPicker2)

  console.log('Benchmark results: ')
  console.log(`Picker 1: ${ms1}`)
  console.log(`Picker 2: ${ms2}`)
})
